.Showcase-Background{
    background-color: #2F1893;
    height: 100%;
    margin-bottom: 20px;
   
}

.Showcase-headingdiv{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.Showcase-imgdiv{
    display: grid;
    grid-template-columns: auto auto ;
}
#but{
    height:100%;
    background-color: transparent;
    border: 1px white solid;
    color: white;
border-radius: 5vh;
margin-top: 2vh;
padding: 1.5vh;
}