.contactbackgrounddiv{
    background-image: url('../../ASSESTS/ContactBackground.png');
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.contactcontent{
    color: white;
    display: flex;
    flex-direction: column;
    margin-right: 5vw;
}
.contactseconddiv{
    background-color: white;
    width: 50vh;
    height: 50vh;
    padding: 3vw;
    margin-top: 20px;
    border-radius: 5px;
}
.contactsecondcontent{
    display: flex;
    flex-direction: row;
   
}
#contactinput{
    width: 15vh;
    margin-top:3vh;
}

.label{
    color: #000;
    font-size: small;
}

.budget{
    padding-left: 1vw;
}
.inputfield{
    border-radius: 1em;
    border: solid 1px rgba(0, 0, 0, 0.251);
    padding: 0.5em;
}

#bud{
    width: 15vh;
    

}
#mess{
    height: 10vh;
}
#sendbut{
    background-color: #25DAC5;
    border-radius: 20px;
    display: flex;
    justify-content: first baseline;
    margin-left: auto;
    border: none;
    padding: 10px 20px;
    font-size: 10px;
}
