body{
    background-color: rgb(55, 55, 180);
   

}

#landing_page{
    background-image:url("../../ASSESTS/Image.png") ;
    height: 100vh;
    margin-bottom: 20px;
    padding-top: 20px;
}
.nav-div{
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
    text-decoration: none;
   
}
.nav-div p a{
    margin: 20px;
    border: 2px;
    text-decoration: none;
    color: aliceblue;
}
.para{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: aliceblue;
    height: 100%;
}
.dots{
    text-align: center;
    justify-content: center;
    text-decoration: none;

}
.account-div{
    text-align: center;
    justify-content: center;
    text-decoration: none;
    margin-top: 50px;
    
}
.account{
    color: aliceblue;
    border-radius: 20px;
    padding: 10px;
    background-color: blue;  
    border: none; 
}
