.CallToAction-Background{
background-image: url('../../ASSESTS/Background.png');
height: 100vh;
justify-content: center;
    align-items: center;
    color: white;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;

}

.CallToAction-content p:first-child{
font-size: 3rem;
}

.CallToAction-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
}


.CallToAction-content p{
    padding: 0px;
    margin: 0px;
    }

    #CallToAction_playicon_background{
        background-color: red;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
        width: 30px;
        height: 30px;
        margin-right: 20px;
    }

    
    .roww{
        border-radius: 15px;
        display: flex;
        flex-direction: row;
        margin-top: 10vh;
    }
    .roww button{
        background-color: aqua;
        border-radius: 30px;
        padding: 0px 30px;
        margin: 0px;
        border:none ;
    }

